import React from 'react';
import Dialog from './Dialog';
import '../CSS/Contact.css';
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import Col from 'react-bootstrap/Col'
import Button from 'react-bootstrap/Button'



export default class Contact extends React.Component {

    constructor(props)
    {
        super(props)
        this.state =
        {
            name: '',
            email: '',
            message: '',
            disabled: false,
            emailSent: null,
            fullName: null,
            isOpen: false
        }
    }

    handleChange = (event) => 
    {
        console.log(event);

        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        })
    }

    handleSubmit = (e) => 
    {
    
    this.setState({isOpen: true})
    e.preventDefault();
    };

    render() {
        
        return (
            <div>

            
            
            <hr className="navLine"></hr>

            <Container className="mb-5">

                <Row className="justify-content-center">

                    <Col xs={11} lg={8} className="containerSquare">

                        <Row>
                            <Col className="text-center fontSquares">
                                <h3>Contact Me</h3> 
                                <h5>montoya0396@gmail.com</h5>
                                <hr className="contactLine"/>
                            </Col>
                        </Row>

                        <form onSubmit={this.handleSubmit}>
                        
                            <Row className="justify-content-around fontSquares mb-5">
                                <Col xs={5}>
                                    <label htmlFor>Your Name</label>Your Name
                                    <input 
                                    value="name"
                                    type="text" 
                                    placeholder="Name" 
                                    onFocus={(e) => e.target.placeholder = ""} 
                                    onBlur={(e) => e.target.placeholder = "Name"}  
                                    required={true} className="contactInput" 
                                    />
                                </Col>

                                <Col xs={5} >
                                    Email Address
                                    <br/>
                                    <input 
                                    type="email" 
                                    placeholder="Email Address" 
                                    onFocus={(e) => e.target.placeholder = ""} 
                                    onBlur={(e) => e.target.placeholder = "Email Address"} 
                                    required={true} className="contactInput" />
                                </Col>
                            </Row>

                            <Row className="pt-3 fontSquares justify-content-center">
                                <Col xs={9} md={10}>
                                    Message
                                    <textarea type="text" required={true} className="contactTextarea"/>
                                </Col>
                            </Row>

                            <Row className="justify-content-center">
                                    <Button type="submit" variant="primary" size="lg" className="mt-5 submitBtn">
                                        Send
                                    </Button>
                            </Row>

                            <Row className="justify-content-center text-white">
                                <p>
                                    Feature not working at the moment. Please contact me directly.
                                </p>
                            </Row>
                        </form>

                    </Col>
                </Row>

                
            </Container>

            <Dialog isOpen={this.state.isOpen} onClose={(e) => this.setState({ isOpen: false})}/>
            </div>
        );
    }   

}